@font-face {
  font-family: FiraCode;
  src: url("/static/fonts/FiraCode-Regular.ttf");
  font-weight: normal;
  font-style: normal;
}

/* Media Queries */
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
}

body {
  color: #212121;
  background-color: #fafafa;
  font-family: "Open Sans", sans-serif;
  line-height: 2em;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #212121;
  font-family: "Abel", sans-serif;
}

code {
  background-color: #000;
  border: 1px solid #404040;
  border-radius: 4px;
  color: #fff;
  font-family: "FiraCode";
  font-weight: lighter;
  padding: 10px 20px;
  margin: 10px 0 0;
}

code::before {
  color: #00cc00;
  content: '$ ';
}

code.windows-code {
  line-height: 3em;
}

code.windows-code::before {
  color: #fff;
  content: 'C:\\ ';
}

li,
ul {
  margin: 0;
  padding: 0;
}

ol {
  margin: 0;
  padding-left: 10px;
}

ul li {
  list-style: none;
}

.site-container {
  margin: auto;
  max-width: 1170px;
  width: 98%;
  margin-bottom: 50px;
}

.homework {
  display: grid;
  margin: auto auto 20px;
  width: 90%;
}

.homework p {
  margin-top: 0;
}

.homework h3 {
  border-bottom: 1px solid #EAECEF;
  padding-bottom: 10px;
  margin-bottom: 25px;
}

.homework ol li span {
  color: #609ec3;
}

.homework ol li span::after {
  content: ': ';
}

@media only screen and (min-width: 768px) {
  .homework {
    grid-gap: 20px;
    grid-template-columns: 1fr 1fr;
  }
  .homework h3 {
    grid-column: 1 / -1;
  }
}

@media only screen and (min-width: 768px) {
  .header-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.gray-container {
  background-color: #f2f2f2;
  border: 1px solid #cccccc;
  border-radius: 4px;
  margin-top: 20px;
  width: 100%;
}

.large-container {
  padding-top: 40px;
  padding-bottom: 50px;
}

.title-container {
  background-color: #F6F8FA;
  border-bottom: 1px solid #DDDDDD;
  padding: 5px 10px;
}

.title-container h2 {
  padding: 0;
  margin: 0;
}

.homework-container {
  border: 1px solid #DDDDDD;
  border-radius: 4px;
  margin-top: 20px;
}

.main-heading {
  margin: auto auto 50px;
  text-align: center;
  width: 90%;
}

@media only screen and (min-width: 768px) {
  .main-heading {
    margin: auto;
    text-align: left;
    width: 80%;
  }
}

.skills {
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  justify-items: center;
  display: grid;
  grid-gap: 10px;
  grid-template-areas: 'html css js' '. gulp .' 'sass npm node';
  margin: auto;
  width: 90%;
}

.skills #html {
  grid-area: html;
}

.skills #css {
  grid-area: css;
}

.skills #js {
  grid-area: js;
}

.skills #sass {
  grid-area: sass;
}

.skills #gulp {
  grid-area: gulp;
}

.skills #npm {
  grid-area: npm;
}

.skills #node {
  grid-area: node;
}

@media only screen and (min-width: 768px) {
  .skills {
    margin: 0;
  }
}

nav ul {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-items: center;
}

nav li {
  cursor: pointer;
  display: inline-block;
  max-width: 100px;
  text-align: center;
}

nav li:hover:not(.active) {
  background: #e5e5e5;
}

nav li:active:not(.active) {
  background-color: #F1F8FF;
  border: 1px solid #C9E2FE;
}

nav li a {
  color: inherit;
  font-family: inherit;
  text-decoration: none;
  padding: 5px 10px;
}

nav .active {
  background-color: #F1F8FF !important;
  border: 1px solid #C9E2FE !important;
}

@media only screen and (min-width: 768px) {
  nav ul {
    display: block;
  }
}

.error-container {
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
  padding: 20px;
}

.error-container a {
  color: #336699;
  font-size: 1.5em;
  font-family: "Abel", sans-serif;
  margin-top: 15px;
  text-align: center;
  text-decoration: none;
}

.error-container img {
  max-width: 100%;
}

@media only screen and (min-width: 768px) {
  .error-container {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    grid-template-columns: 1fr 1fr;
  }
  .error-container a {
    color: #538cc6;
    margin-top: 0;
  }
  .error-container a:hover {
    color: #336699;
  }
  .error-container a:active {
    padding-top: 5px;
  }
}

.justify-text {
  text-align: justify !important;
}

@media only screen and (min-width: 768px) {
  .no-margin-bottom {
    margin-bottom: 0 !important;
  }
}
